import { IconLogoShopee } from "components/icons";
import React from "react";

const HeaderAuth = ({ heading = "" }) => {
  return (
    <div className="w-full h-[84px] py-4 hidden lg:block">
      <div className="max-w-[1280px] mx-auto flex items-center justify-between">
        <div className="flex items-center gap-x-4">
          <IconLogoShopee fill="#f6432d"></IconLogoShopee>
          <h3 className="text-2xl leading-[29px] text-[#222222] mt-[15px]">
            {heading}
          </h3>
        </div>
        <p className="text-sm text-primary mt-[22px]">Bạn cần giúp đỡ ?</p>
      </div>
    </div>
  );
};

export default HeaderAuth;
