import LayoutAuthentication from "layout/LayoutAuthentication";
import SignUpAuth from "modules/auth/SignUpAuth";
import SignUpResponsive from "modules/authResponsive/SignUpResponsive";
import Footer from "modules/footer/Footer";
import HeaderAuth from "modules/header/HeaderAuth";
import React from "react";
import { Fragment } from "react";

const SignUpPage = () => {
  return (
    <Fragment>
      <div className="hidden lg:block">
        <LayoutAuthentication>
          <HeaderAuth heading="Đăng ký"></HeaderAuth>
          <SignUpAuth></SignUpAuth>
          <Footer></Footer>
        </LayoutAuthentication>
      </div>
      <div className="block lg:hidden">
        <SignUpResponsive></SignUpResponsive>
      </div>
    </Fragment>
  );
};

export default SignUpPage;
