import React from "react";
import { useController } from "react-hook-form";
import { withErrorBoundary } from "react-error-boundary";
import ErrorComponent from "components/common/ErrorComponent";
import PropTypes from "prop-types";

const Input = (props) => {
  const {
    control,
    name,
    type = "text",
    children,
    placeholder,
    error = "",
    ...rest
  } = props;
  const { field } = useController({
    control,
    name,
    defaultValue: "",
  });
  return (
    <div>
      <input
        id={name}
        autoComplete="off"
        type={type}
        className={`w-full py-2 px-3 text-sm bg-white border text-[rgba(0,_0,_0,_0.8)] rounded  transition-all placeholder:text-[12px]
        ${error.length > 0 ? "border-error" : "border-gray-300"}`}
        placeholder={placeholder}
        name={name}
        {...rest}
        {...field}
      />

      {error.length > 0 && (
        <span className="mt-2 text-sm pointer-events-none text-error">
          {error}
        </span>
      )}
    </div>
  );
};

Input.propTypes = {
  name: PropTypes.string,
  type: PropTypes.string,
  error: PropTypes.string,
  control: PropTypes.any.isRequired,
};

export default withErrorBoundary(Input, {
  FallbackComponent: ErrorComponent,
});
