import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";

const Button = (props) => {
  const {
    onClick,
    type = "button",
    className = "",
    isLoading = false,
    children,
    ...rest
  } = props;

  const classNameDefault =
    "flex items-center justify-center p-4 text-base font-semibold rounded-xl min-h-[56px] bg-primary text-white uppercase w-full";
  const child = !!isLoading ? (
    <div className="w-10 h-10 border-4 border-white rounded-full border-t-transparent border-b-transparent animate-spin"></div>
  ) : (
    children
  );
  if (rest.href)
    return (
      <Link to={rest.href} className={classNameDefault}>
        {child}
      </Link>
    );
  return (
    <button
      className={`${classNameDefault} ${
        !!isLoading ? "opacity-50 pointer-events-none" : ""
      } ${className}`}
      onClick={onClick}
      type={type}
      {...rest}
    >
      {child}
    </button>
  );
};

Button.propTypes = {
  type: PropTypes.string,
  className: PropTypes.string,
  children: PropTypes.node,
  isLoading: PropTypes.bool,
  href: PropTypes.string,
};

export default Button;
